<template>
  <div class="list" ref="warpper">
      <div class="area">
        <div class="item-list" v-for="item of users" :key="item.id" :ref="item.id">
          <div class="item border-bottom" @click="handleUserClick(item)">
            <img src="../imgs/touxiang.jpg" alt="头像">
            {{item.name}}
            <div class="item-delete" @click.stop @click="handleUserDelete(item)">x</div>
          </div>
        </div>
      </div>
      <router-link to="/create">
        <div class="adduser">
          <img src="../imgs/add.jpg" alt="添加">
        </div>
      </router-link>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapState, mapMutations } from 'vuex'
import axios from 'axios'
export default {
  name: 'CityList',
  props: {
    users: Array,
    letter: String
  },
  mounted () {
    // 这里不加click: true手机上点击会有问题
    this.scroll = new Bscroll(this.$refs.warpper, {click: true})
  },
  watch: {
    letter () {
      if (this.letter) {
        // v-for循环出来导致得到的其实是一个数组，不是标准dom元素,可以用[0]
        const element = this.$refs[this.letter][0]
        // 滚到某个元素
        this.scroll.scrollToElement(element)
      }
    }
  },
  methods: {
    handleUserClick (user) {
      // 用dispath向actions传递信息
      // this.$store.dispatch('changeCity', city)
      // 用mapMutations
      this.changeUser(user)
      // 编程式路由导航
      this.$router.push('/Modify')
    },
    handleUserDelete (user) {
      const params = new URLSearchParams()
      params.append('id', user.id)
      params.append('name', user.name)
      params.append('phonenumber', user.phone)
      params.append('email', user.email)
      axios.post('api/deleteUser', params)
        .then(function (res) {
          const data = res.data
          alert(data.desc)
        })
      this.$emit('delete')
    },
    ...mapMutations(['changeUser'])
  },
  computed: {
    ...mapState({
      currentUser: 'user'
    })
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
  // 改变1像素边框默认颜色
  .border-topbottom
    &:before
      border-color #cccccc
    &:after
      border-color #cccccc
  .border-bottom
    &:before
      border-color #cccccc
  .list
    // 这里用绝对定位的方法让页面无法滚动，用bottom 0来限定高度，配合overflow hidden就能让页面无法滚动
    position absolute
    top 1.58rem
    left 0
    right 0
    bottom 0
    overflow hidden
    .item-list
      height 1.5rem
      .item
        line-height 1.5rem
        padding-left .2rem
        img
          padding-right .2rem
          height 95%
          width 1.5rem
        .item-delete
          float right
          font-size .5rem
          padding-right .3rem
          opacity .3
  .adduser
    position fixed
    z-index 99
    opacity 0.3
    bottom 1rem
    right 1rem
    width 1rem
    height 1rem
    img
      height 100%
      width 100%
</style>



