<template>
  <div>
    <city-header></city-header>
    <city-search :users="users"></city-search>
    <city-list :users="users" :letter="letter" @delete="handleDelete"></city-list>
    <!-- <city-alphabet :users="users" @alphabet="handleLetterChange"></city-alphabet> -->
  </div>
</template>

<script>
import CityHeader from './component/Header'
import CitySearch from './component/Search'
import CityList from './component/List'
import axios from 'axios'
export default {
  name: 'City',
  components: {
    CityHeader,
    CitySearch,
    CityList
  },
  data () {
    return {
      // 所有人的信息
      // A:[],B:[],C:[],D:[],E:[],F:[],G:[],H:[],I:[],
      //   K:[],L:[],M:[],N:[],O:[],P:[],Q:[],R:[],S:[],
      //   T:[],U:[],V:[],W:[],X:[],Y:[],Z:[]
      users: [],
      // alphabet向list传的值
      letter: ''
    }
  },
  mounted () {
    this.getCityInfo()
  },
  methods: {
    // 发送ajax请求
    getCityInfo () {
      axios.get('api/getUser')
        .then(this.handleCityInfoSucc)
    },
    // 处理ajax返回数据
    handleCityInfoSucc (res) {
      res = res.data
      if (res) {
        const data = res
        // const data = res.data
        this.users = data.sort((x, y) => {
          return x['name'].localeCompare(y['name'])
        })
      }
    },
    handleLetterChange (letter) {
      // console.log(letter)
      this.letter = letter
    },
    // 处理删除成功后的数据刷新
    handleDelete () {
      this.getCityInfo()
      // alert('sdasdasd')
    }
  }
}
</script>

<style lang="stylus" scoped>

</style>



